.status-bar{
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    padding: 5px 3px;
}

.status-bar__column{
    width: 33%;
    display: flex;
    align-items: center;
}

.status-bar__column:first-child span{
    margin-right: 5px;
}

.status-bar__column:nth-child(2){
    display: flex;
    justify-content: center;
}

.status-bar__column:last-child{
    display: flex;
    justify-content: flex-end;
}

.fa-battery-full{
    margin: 0 5px;
}